import React, { useRef, useState } from "react";
import "../App.css";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Paper from "@material-ui/core/Paper";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import PersonIcon from "@material-ui/icons/Person";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import { motion } from "framer-motion";
import { Link as ReactLink, useHistory } from "react-router-dom";
import { useAuth } from "../contexts/AuthContext";

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright © "}
      <ReactLink className="link" to="/home">
        Fryvoker design
      </ReactLink>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    height: "100vh",
  },
  image: {
    backgroundImage: "url(https://source.unsplash.com/collection/2393384/)",
    backgroundRepeat: "no-repeat",
    backgroundColor:
      theme.palette.type === "light"
        ? theme.palette.grey[50]
        : theme.palette.grey[900],
    backgroundSize: "cover",
    backgroundPosition: "center",
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  link: {
    color: "#717070",
    textDecoration: "none",
    userSelect: "none",
    cursor: "pointer",
    transition: "all 0.2s ease-in-out",
    "&:hover": {
      color: "#333",
      transition: "all 0.2s ease-in-out",
    },
  },
  text: {
    color: "#0f0f0f",
    cursor: "default",
    userSelect: "none",
  },
  inputField: {
    textDecoration: "none",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: "#0f0f0f",
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    backgroundColor: "#0f0f0f",
    "&:hover": {
      backgroundColor: "#1a1a1a",
    },
    laber: {
      border: "none",
    },
  },
}));

function Signup() {
  const classes = useStyles();
  const emailRef = useRef();
  const passwordRef = useRef();
  const passwordConfirmRef = useRef();
  const { signup } = useAuth();
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const history = useHistory();

  async function handleSubmit(e) {
    e.preventDefault();
    if (emailRef.current.value === "") {
      return setError("Вы не ввели Вашу почту!");
    } else if (passwordRef.current.value === "") {
      return setError("Вы не ввели пароль!");
    } else if (passwordConfirmRef.current.value === "") {
      return setError("Вы не повторили пароль!");
    } else if (passwordRef.current.value !== passwordConfirmRef.current.value) {
      return setError("Пароли не совпадают!");
    }

    try {
      setError("");
      setLoading(true);
      await signup(emailRef.current.value, passwordRef.current.value);
      history.push("/login");
    } catch {
      setError("Не получилось создать аккаунт!");
    }

    setLoading(false);
  }
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ delay: 1.5 }}
    >
      <Grid container component="main" className={classes.root}>
        <CssBaseline />
        <Grid item xs={false} sm={4} md={7} className={classes.image} />
        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <div className={classes.paper}>
            <Avatar className={classes.avatar}>
              <PersonIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
              Создайте аккаунт
            </Typography>
            <form onSubmit={handleSubmit} className={classes.form} noValidate>
              <TextField
                className={classes.inputField}
                variant="outlined"
                margin="normal"
                fullWidth
                id="email"
                label="Email"
                name="email"
                type="email"
                autoComplete="email"
                inputRef={emailRef}
              />
              <TextField
                className={classes.inputField}
                variant="outlined"
                margin="normal"
                fullWidth
                name="password"
                label="Пароль"
                type="password"
                id="password"
                inputRef={passwordRef}
              />
              <TextField
                className={classes.inputField}
                variant="outlined"
                margin="normal"
                fullWidth
                name="password-confirm"
                label="Пароль"
                type="password"
                id="password-confirm"
                inputRef={passwordConfirmRef}
              />
              <Button
                disabled={loading}
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
              >
                Создать аккаунт
              </Button>
              {error && (
                <motion.div
                  initial={{ opacity: 0, x: "100vh" }}
                  animate={{ opacity: 1, x: 0 }}
                  className="error"
                >
                  {error}
                </motion.div>
              )}
              <Grid container>
                <Grid item xs>
                  <div className={classes.text} variant="body2">
                    Попали не туда куда хотели? Просто перейдите
                    <ReactLink className={classes.link} to="/home">
                      {" "}
                      на главную страницу :D
                    </ReactLink>
                  </div>
                  <br />
                  <div className={classes.text} variant="body2">
                    Вы сотрудник и у Вас уже есть аккаунт?{" "}
                    <ReactLink className={classes.link} to="/login">
                      Тогда просто войдите в него 😉
                    </ReactLink>
                  </div>
                </Grid>
              </Grid>
              <Box mt={5}>
                <Copyright />
              </Box>
            </form>
          </div>
        </Grid>
      </Grid>
    </motion.div>
  );
}

export default Signup;
